import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex) // vue

// Vuex.Store 
const store = new Vuex.Store({
    // datastate$
    state: {
        // 
        $userInfo: {
            id: 1
        },
		$StockManageInfo:{}
    },
	mutations:{
		SET_STOCK_MANAGE_INFO(state,data){
			state.$StockManageInfo=Object.assign({},state.$StockManageInfo,data)
		}
	}
})

export default store
